<template>
  <div>
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>申报管理
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon> 实施方案
      </BreadcrumbItem>
      <span class="screenContent">
        <img style="width: 11px;margin-right: 8px;margin-left: 10px;" src="../assets/day.png">
        {{ curTime }}</span>
    </Breadcrumb>
    <Content class="content" :style="{padding: '14px 24px', background: '#fff'}">
      <Button type="primary" :disabled="projectList.length===1" icon="md-add" @click="addTableInfo">
        新增
      </Button>
      <Button type="primary" icon="ios-create" @click="modifyData">
        修改
      </Button>
      <Button type="primary" icon="md-close" @click="deleteData">
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(11)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/project/exportProjectListExcel')">
        导出
      </Button>
      <Table ref="checkTable" highlight-row class="table" :columns="columns" :data="projectList" @on-current-change="selectRow"></Table>
      <!--详情弹出框-->
      <Modal
        v-model="isPopUp"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span>请填写实施方案信息</span>
        </p>
        <Form ref="form" :model="form" :rules="formRules" :label-width="80">
          <Tabs :animated="false" class="innerTabStyle" :value="curTab" @on-click="clickTab">
            <TabPane label="基本信息" name="name1">
              <Row>
                <Col span="24">
                <FormItem label="项目名称" prop="projectName">
                  <Input v-model.trim="form.projectName" :disabled="!isbtnShow" placeholder="请填写项目名称"></Input>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem label="承担单位">
                  <Input v-model.trim="userInfo.orgName" disabled></Input>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem label="资金类别">
                  <Input v-model.trim="userInfo.capitalName" disabled></Input>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem label="主管单位">
                  <Input v-model.trim="userInfo.manageOrgName" disabled></Input>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem label="实施地点" prop="implementSite">
                  <Input v-model.trim="form.implementSite" :disabled="!isbtnShow" placeholder="请填写实施地点"></Input>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="24">
                <FormItem label="工作内容" prop="workContent">
                  <Input v-model.trim="form.workContent" :disabled="!isbtnShow" type="textarea" :autosize="{minRows: 1,maxRows: 5}" placeholder="请填写工作内容"></Input>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="24">
                <FormItem label="实施进度" prop="implementJindu">
                  <Input v-model.trim="form.implementJindu" :disabled="!isbtnShow" type="textarea" :autosize="{minRows: 1,maxRows: 5}" placeholder="请填写实施进度"></Input>
                </FormItem>
                </Col>
              </Row>

            </TabPane>
            <TabPane label="资金构成" name="name2">
              <Row>
                <Col span="12">
                <FormItem label="总投资" prop="allFinancial">
                  <Input v-model.trim="form.allFinancial" :disabled="!isbtnShow" placeholder="请填写总投资" number>
                  <span slot="append">万元</span>
                  </Input>
                </FormItem>
                </Col>
                <Col span="12">
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem label="中央财政" prop="centralFinancial">
                  <Input v-model.trim="form.centralFinancial" :disabled="!isbtnShow" placeholder="请填写中央财政" number>
                  <span slot="append">万元</span>
                  </Input>
                </FormItem>
                </Col>
                <Col span="12">
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem label="市级财政" prop="cityFinancial">
                  <Input v-model.trim="form.cityFinancial" :disabled="!isbtnShow" placeholder="请填写市级财政" number>
                  <span slot="append">万元</span>
                  </Input>
                </FormItem>
                </Col>
                <Col span="12">
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem label="区级财政" prop="districtFinancial">
                  <Input v-model.trim="form.districtFinancial" :disabled="!isbtnShow" placeholder="请填写区级财政" number>
                  <span slot="append">万元</span>
                  </Input>
                </FormItem>
                </Col>
                <Col span="12">
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem label="其他资金" prop="elseFinancial">
                  <Input v-model.trim="form.elseFinancial" :disabled="!isbtnShow" placeholder="请填写其他资金" number>
                  <span slot="append">万元</span>
                  </Input>
                </FormItem>
                </Col>
              </Row>
            </TabPane>
            <TabPane label="效益目标" name="name3">
              <Col span="24">
              <FormItem label="工作目标" prop="workTarget">
                <Input v-model.trim="form.workTarget" :disabled="!isbtnShow" type="textarea" :autosize="{minRows: 1,maxRows: 5}" placeholder="请填写工作目标"></Input>
              </FormItem>
              </Col>
              <Col span="24">
              <FormItem label="预期效益">
                <Input v-model.trim="form.expectBenefit" :disabled="!isbtnShow" type="textarea" :autosize="{minRows: 1,maxRows: 5}" placeholder="请填写预期效益"></Input>
              </FormItem>
              </Col>
            </TabPane>
            <TabPane class="uploadStyle" label="附件" name="name4">
              <uploadFile ref="uploadFile" :is-disabled="!isbtnShow" :form="uploadFile" />
            </TabPane>
          </Tabs>
        </Form>
        <p slot="footer" style="text-align: left">
          <Button icon="md-print">
            打印
          </Button>

          <Button icon="md-exit">
            导出
          </Button>
          <Button v-if="isbtnShow" style="float: right;margin-left: 8px" @click="isPopUp = false">
            取消
          </Button>
          <!--基本信息|资金构成|效益目标|附件-->
          <Button v-if="isbtnShow" style="float: right" type="primary" @click="submitTarget()">
            确认
          </Button>
        </p>
      </Modal>
    </Content>
    <!--审核的弹出框-->
    <audit ref="audit" :form="auditForm" />
  </div>
</template>

<script>
import audit from '@/components/audit.vue'
import uploadFile from '@/components/fileUpload.vue'
import { dateformat } from '@/filters/index.js'
import excelExcel from '@/mixins/exportExcel.js'
export default {
  name: 'BudgetManage',
  components: {
    audit,
    uploadFile
  },
  mixins: [excelExcel],
  data() {
    return {
      uploadFile: {
        fileids: null
      },
      auditForm: {
        // 审核人
        auditMan: '',
        // 审核结果
        result: { color: 'blue', name: '正在审核' },
        // 审核意见
        auditMessage: '',
        fileids: null
      },
      userInfo: this.$store.state.user.userInfo,
      curTab: 'name1',
      // 当查看详情时，添加 按钮隐藏
      isbtnShow: true,
      // 添加弹出框
      form: {
        // 项目名称
        projectName: '',
        // 实施地点
        implementSite: '',
        // 工作内容
        workContent: '',
        // 实施进度
        implementJindu: '',
        // 总投资
        allFinancial: '',
        // 中央财政
        centralFinancial: '',
        // 市级财政
        cityFinancial: '',
        // 区级财政
        districtFinancial: '',
        // 其他资金
        elseFinancial: '',
        // 工作目标
        workTarget: '',
        // 预期效益
        expectBenefit: '',
        projectFileIds: null,
        fileids: null
      },
      formRules: {
        implementSite: [
          { type: 'string', required: true, message: '请填写实施地点', trigger: 'change' }
        ],
        workContent: [
          { required: true, message: '请填写工作内容', trigger: 'change' }
        ],
        implementJindu: [
          { required: true, message: '请填写实施进度', trigger: 'change' }
        ],
        projectName: [
          { required: true, message: '请填写项目名称', trigger: 'change' }
        ],
        workTarget: [
          { required: true, message: '请填写工作目标', trigger: 'change' }
        ],
        // 2
        allFinancial: [
          { type: 'number', required: true, message: '请填写总投资', trigger: 'change' }
        ],
        centralFinancial: [
          { type: 'number', required: true, message: '请填写中央财政', trigger: 'change' }
        ],
        districtFinancial: [
          { type: 'number', required: true, message: '请填写区级财政', trigger: 'change' }
        ],
        cityFinancial: [
          { type: 'number', required: true, message: '请填写市级财政', trigger: 'change' }
        ],
        elseFinancial: [
          { type: 'number', required: true, message: '请填写其他资金', trigger: 'change' }
        ]
      },
      curTime: '',
      isPopUp: false,
      transferValue: true,
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left'
        },
        {
          title: '实施方案',
          key: 'projectName',
          render: (h, params) => {
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.showInfo(params.row);
                }
              }
            }, params.row.projectName);
          }
        },
        {
          align: 'center',
          title: '审核状态',
          key: 'auditStatus',
          fixed: 'right',
          width: 120,
          render: (h, params) => {
            const row = params.row
            const status = row.auditStatus
            const audit = this.status[status]
            const buttons = [
              h('Button', {
                style: {
                  color: audit.color + '!important'
                },
                props: {
                  type: 'text',
                  size: 'small'
                },
                on: {
                  click: () => {
                    const auditTime = row.auditTime ? dateformat(row.auditTime, 'YYYY年MM月DD日 HH:mm:ss') : ''
                    if (status === 2 || status === 1) {
                      // 赋值
                      this.auditForm = {
                        // 审核人
                        auditMan: row.auditMan,
                        // 审核结果
                        result: audit,
                        // 审核意见
                        auditMessage: row.auditMessage,
                        auditTime: auditTime,
                        fileids: row.fileids
                      }
                      this.$refs.audit.displayAudit()
                    } else {
                      this.showCheck(row)
                    }
                  }
                }
              }, audit.name)
            ]
            // 如果是审核驳回的话，多添加一个按钮，再次提交
            if (status === 2) {
              buttons.push(h('Button', {
                style: {
                  color: 'green'
                },
                props: {
                  type: 'text',
                  size: 'small'
                },
                on: {
                  click: () => {
                    this.showCheck(row)
                  }
                }
              }, '再次提交'))
            }
            return h('div', buttons);
          }
        }
      ],
      projectList: [],
      status: {
        0: {
          name: '正在审核',
          color: 'blue'
        },
        1: {
          name: '审核通过',
          color: 'green'
        },
        2: {
          name: '审核未通过',
          color: 'red'
        },
        3: {
          name: '未审核',
          color: 'orange'
        }
      },
      // 是否选择行
      isSelected: false,
      isCreate: false
    }
  },
  mounted() {
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.getProjectList();
    this.curTime = time.substring(0, 15)
  },
  methods: {
    clickTab(name) {
      this.curTab = name
    },
    submitTarget() {
      const that = this
      const form = that.form
      form.fileIds = that.$refs.uploadFile.getFileIds()
      that.$refs.form.validate((valid) => {
        if (valid) {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          // 创建
          if (that.isCreate) {
            that.commonUtil.ajaxPostUtil('/project/addProject', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.isPopUp = false;
                that.$Message.success(res.msg);
                that.getProjectList()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          } else {
            that.commonUtil.ajaxPutUtil('/project/updateProject', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.isPopUp = false;
                that.$Message.success(res.msg);
                that.getProjectList()
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    },
    // 获取实施方案列表
    getProjectList() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxPostUtil('/project/searchProject', { auditBatchType: 2 }, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.projectList = res.data
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
      that.commonUtil.getAuditCountApi(that)
      that.isSelected = false
      that.currentRow = {}
    },
    // 删除实施方案
    deleteData() {
      const that = this;
      const row = that.currentRow
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要删除的内容');
        return
      }
      if ([2, 3].indexOf(row.auditStatus) === -1) {
        that.$Message.warning('该审核状态下不能删除数据');
        return
      }
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/project/deleteProject/' + row.projectId, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.isSelected = false;
              that.$Message.success(res.msg);
              that.getProjectList()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            this.$Message.error('网络连接错误');
            this.$Spin.hide();
          }, that)
        }
      });
    },
    clearFormData() {
      this.form = {
        // 项目名称
        projectName: '',
        // 实施地点
        implementSite: '',
        // 工作内容
        workContent: '',
        // 实施进度
        implementJindu: '',
        // 总投资
        allFinancial: '',
        // 中央财政
        centralFinancial: '',
        // 市级财政
        cityFinancial: '',
        // 区级财政
        districtFinancial: '',
        // 其他资金
        elseFinancial: '',
        // 工作目标
        workTarget: '',
        // 预期效益
        expectBenefit: ''
      }
      this.$refs.form.resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    },
    // 新增表格内容
    addTableInfo() {
      this.clearFormData();
      this.curTab = 'name1'
      this.isCreate = true;
      this.isbtnShow = true;
      this.isPopUp = true;
    },
    // 选中一行进行修改数据
    selectRow(currentRow) {
      this.currentRow = currentRow
      this.isSelected = true;
    },
    modifyData() {
      const that = this;
      if (!that.isSelected) {
        that.$Message.warning('您未选择需要修改的内容');
        return
      }
      const row = Object.assign({}, that.currentRow);
      const auditStatus = row.auditStatus
      // 未审核 和 审核驳回可以更改信息
      const flag = [2, 3].indexOf(auditStatus) !== -1
      that.curTab = 'name1'
      that.isCreate = false;
      that.isbtnShow = flag;
      this.showData(row)
    },
    // 打开详情弹出来
    showInfo(row) {
      const data = Object.assign({}, row)
      // "添加“按钮隐藏
      this.curTab = 'name1'
      this.isbtnShow = false;
      this.showData(data)
    },
    showData(data) {
      this.form = data
      this.uploadFile.fileids = data.projectFileIds
      this.$nextTick(() => {
        this.$refs.uploadFile.getFileItems()
      })
      this.isPopUp = true
    },
    showCheck(row) {
      const that = this;
      if (row.auditStatus === 1) {
        that.$Message.error('项目已经审核通过，无需继续提交');
        return;
      }
      that.$Modal.confirm({
        title: '是否确认提交审核',
        content: '',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxPutUtil('/auditBatch/updateAuditBatch', { auditBatchId: row.auditBatchId, auditStatus: 0 }, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.getProjectList()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        }
      });
    }
  }
}
</script>



